.postWrapper{
    border-bottom: 1px rgb(47, 51, 54) solid ;
    width: 100%;
    padding: 12px 16px 0 16px;
    display: flex;
    box-sizing: border-box;
}
.profilePictureColumn{
    min-width: 40px;
    width: 40px;
    margin-right: 12px;
}
.profilePictureColumn img{
    width: 100%;
    border-radius: 100%;
    user-select: none;
}
.dataColumn{
    width: 100%;
    box-sizing: border-box;
}
.likeContainer{
    width: fit-content;
}
.likeWrapper{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.likeCounter{
    font-size: 13px;
    color: rgb(113, 118, 123);
    margin-left: -12px;
    user-select: none;
}
.likeCounter span{
    color: inherit;
}
.liked{
    color: rgb(249, 24, 128);
}
.textContainer{
    font-size: 15px;
    min-width: 0;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    word-wrap: break-word;
    margin-bottom: 4px;
}
.nameContainer{
    font-size: 15px;
    font-weight: 700;
    margin-bottom: 2px;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}